<template>
  <div class="detail">
    <van-nav-bar title="商品详情" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="detail-container">
      <div>
        <img class="auto-img" :src="productData.large_img" alt />
      </div>
      <div class="detail-content">
        <div class="detail-info">
          <span class="pro-name">{{productData.name}}</span>
        </div>
        <!-- 商品规格 -->
        <div class="rule-box">
          <div class="rule-item clearfix" v-for="(item,index) in productData.rules" :key="index">
            <div class="fl rule-title">{{item.title}}</div>
            <div class="rule-tags clearfix fl">
              <div
                class="tag-item fl"
                :class="{active:v.isActive}"
                v-for="(v,i) in item.tag"
                :key="i"
                @click="toggleRuleTag(item,v)"
              >{{v.name}}</div>
            </div>
          </div>
        </div>
        <!-- 商品描述 -->
        <div class="desc-box">
          <div class="desc-title">商品描述</div>
          <div class="desc-content">
            <div
              class="desc-item"
              v-for="(item,index) in this.productData.desc"
              :key="index"
            >{{item}}</div>
          </div>
        </div>
        <div class="price-box clearfix">
          <div class="price fl">￥{{productData.price}}</div>
          <div class="fr">
            <van-stepper v-model="count" theme="round" button-size="22" disable-input />
          </div>
        </div>
      </div>
    </div>

    <van-goods-action>
      <van-goods-action-icon
        icon="cart-o"
        text="购物车"
        @click="goShopcart"
        :badge="shopcartCounts == 0 ? '' : shopcartCounts"
      />
      <van-goods-action-icon
        icon="like"
        :text="color ? '已收藏' : '收藏'"
        :color="color ? 'brown' : '#ccc'"
        @click="like"
      />
      <van-goods-action-button
        color="rgb(215, 180, 148)"
        type="warning"
        text="加入购物车"
        @click="addShopcart"
      />
      <van-goods-action-button color="brown" type="danger" text="立即购买" @click="addShopcart(true)" />
    </van-goods-action>
  </div>
</template>

<script>
import Vue from "vue";
import { Toast } from "vant";
export default {
  data() {
    return {
      count: 1,
      //商品id
      pid: "",
      //商品详情数据
      productData: "",
      color: false,
      //购物车里面的商品数量
      shopcartCounts: 0
    };
  },
  created() {
    //截取查询参数
    this.pid = this.$route.query.pid;
    this.getProductDetail(this.pid);
    this.findLike(this.pid);
    this.findShopcart();
  },
  methods: {
    onClickLeft() {
      // Toast("返回");
      this.$router.go(-1);
    },
    getProductDetail(pid) {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          pid,
          appkey: this.appkey
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 600) {
            let data = result.data.result[0];
            data.desc = data.desc.split(/\n/);
            //商品规格
            let productRule = ["tem", "sugar", "milk", "cream"];
            let rules = [];
            productRule.forEach(v => {
              if (data[v] != "") {
                let rule = {
                  title: data[v + "_desc"],
                  tag: []
                };
                let tag = data[v].split(/\//);
                tag.forEach((item, i) => {
                  let t = {
                    name: item,
                    isActive: i == 0
                  };
                  rule.tag.push(t);
                });
                rules.push(rule);
              }
            });
            data.rules = rules;
            this.productData = data;
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //切换规格
    toggleRuleTag(item, v) {
      if (v.isActive) {
        return;
      }
      //查找已经激活的规格标签
      for (let i = 0; i < item.tag.length; i++) {
        if (item.tag[i].isActive) {
          item.tag[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },
    //收藏商品
    like() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      //请求路径，收藏like，取消收藏notlike
      let url = this.color ? "/notlike" : "/like";
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: url,
        data: {
          pid: this.pid,
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 800 || result.data.code == 900) {
            this.color = result.data.code == 800;
            this.$toast(result.data.msg);
          } else {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //查询当前商品是否被收藏
    findLike(pid) {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          pid,
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 1000) {
            this.color = result.data.result.length > 0;
          } else {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //查询购物车商品数量
    findShopcart() {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 5000) {
            this.shopcartCounts = result.data.result.length;
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    addShopcart(isBuy) {
      //获取token
      let tokenString = localStorage.getItem("__TK");
      //获取商品规格
      let rules = this.productData.rules;
      let rule = [];
      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break;
          }
        }
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
          count: this.count,
          rule: rule.join("/")
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.shopcartCounts++;
            }
            this.$toast(result.data.msg);
            //如果是立即购买, 先将商品加入到购物车，在跳转到结算页面(提交订单页面)
            if (isBuy === true) {
              setTimeout(() => {
                this.$router.push({
                  name: "Pay",
                  query: { sids: result.data.sid }
                });
              }, 400);
            }
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //跳转到购物车
    goShopcart() {
      this.$router.push({ name: "Shopcart" });
    }
  }
};
</script>

<style lang="less" scoped>
.detail {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0px;
  right: 0;
  .detail-container {
    overflow-y: auto;
    height: calc(~"100% - 111.63px");
  }
  .detail-content {
    padding: 10px;
    background-color: #fff;
  }
  .detail-info {
    margin-top: 10px;
    .pro-name {
      font-size: 16px;
      line-height: 24px;
    }
  }
  .rule-box {
    margin-top: 20px;
    padding-bottom: 10px;
    font-size: 14px;
    border-bottom: 1px solid #ccc;
    .rule-title {
      height: 26px;
      line-height: 26px;
      width: 60px;
      color: #444;
    }
    .tag-item {
      width: 80px;
      height: 26px;
      line-height: 26px;
      text-align: center;
      box-sizing: border-box;
      border: 1px solid rgb(215, 180, 148);
      border-radius: 5px;
      margin-right: 10px;
      margin-bottom: 10px;
      color: rgb(215, 180, 148);
      &:last-child {
        margin-right: 0;
      }
      &.active {
        border: 1px solid brown;
        color: brown;
      }
    }
  }
  .desc-box {
    margin-top: 10px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 20px;
    .desc-title {
      font-size: 16px;
      color: bule;
    }
    .desc-content {
      color: #666;
      font-size: 14px;
      .desc-item {
        padding-top: 10px;
      }
    }
  }
  .price-box {
    padding: 20px 0 10px;
    .price {
      color: brown;
      font-size: 16px;
      line-height: 22px;
    }
  }
}

/deep/ .van-stepper__plus {
  background-color: #fff;
  border: 1px solid brown;
  border-radius: 0;
  color: brown;
}
/deep/ .van-stepper__minus {
  background-color: #fff;
  border-radius: 0;
  border: 1px solid rgb(215, 180, 148);
  color: rgb(215, 180, 148);
}
/deep/ .van-info {
  background-color: brown;
}
/deep/ .van-nav-bar__text {
  color: brown;
}
/deep/ .van-nav-bar .van-icon {
  color: brown;
}
</style>